.upload-container {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
}

.upload-heading {
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
}

.upload-card {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: inline-block;
    width: 100%;
}

.file-input {
    margin-bottom: 20px;
    padding: 10px;
    background-color: #f0f0f0;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 100%;
}

.file-info {
    margin: 10px 0;
    font-size: 14px;
    color: #555;
}

.file-name {
    font-weight: bold;
    display: inline-block;
    margin-right: 10px;
}

.file-size {
    color: #888;
}

.upload-btn {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

    .upload-btn:hover {
        background-color: #0056b3;
    }

    .upload-btn.disabled {
        background-color: #ccc;
        cursor: not-allowed;
    }

.error-message {
    margin-top: 10px;
    color: red;
    font-size: 14px;
}

.progress-bar {
    width: 100%;
    background-color: #f3f3f3;
    border: 1px solid #ccc;
    border-radius: 5px;
    overflow: hidden;
    margin: 10px 0;
    height: 20px;
}

.progress-bar-fill {
    height: 100%;
    background-color: #4caf50;
    width: 0;
    transition: width 0.4s ease;
}
